.side-nav{
	padding:30px;
	height:350px;
	background:linear-gradient(to bottom ,var(--color-one), white);
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	right:0;
	width:70%;
	max-width:300px;
	width:300px;
	z-index:300;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
	border-bottom-left-radius: 400px;
}
.hide{
	-webkit-transform:translateX(+100%);
	        transform:translateX(+100%);
}

